
import { BaseEntity } from "../../masters/base-entity";
import { InventoryAccount } from "./inventory-account";
import { ItemClass } from "./Item-class";
import { UOM } from "./UOM";

export class ItemCategory extends BaseEntity{
	constructor(){
		super();
	}
	itemCategoryId: number;
	code: string;
	description: string;
	isActive: boolean;
	isGroup: boolean; 
	parentId: number;
	itemClass : ItemClass = new ItemClass();
	salesAccount: InventoryAccount = new InventoryAccount();
	purchAndInvenAccount: InventoryAccount = new InventoryAccount();
	costOfSalesAccount: InventoryAccount = new InventoryAccount();
	stockControlAccount: InventoryAccount = new InventoryAccount();
	trackUOM : UOM = new UOM;
	baseUOM : UOM = new UOM;
	isInventoried: boolean; 
	isBatchTracked: boolean;
	idSerialized: boolean;
	categoryTypeNum : number;
	defaultPricingMethod: number;
	defaultMarkupPer : number;
	shelfLifeLimit: number;
	
	
	

}