import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { routerTransition } from '../../router.animations';
import { Department } from './masters/department';
import { DepartmentService } from './services/department.service';
import {BranchServiceService} from '../service-center/services/branch-service.service';
import { Branch } from '../service-center/masters/branch';

@Component({
  selector: 'department',
  templateUrl: './department.component.html',
  styleUrls: ['./department.component.css'],
  animations: [routerTransition()]
})
export class DepartmentComponent implements OnInit{
	
	search = '';
	len : number;
	page =1;
	pageSize =5;
	departments : Department;
	submitted: boolean = true;
	submit : boolean = true;
	dep: any = '';
	id1 :number;
	department: Department;
	branchList: Array<Branch>;
	branchId : number;
	
	constructor(private http:HttpClient,private route :ActivatedRoute,private branchService: BranchServiceService, private router : Router, private departmentService : DepartmentService){}
	ngOnInit(): void
	{
		this.reloadData();
  	}
	
	reloadData() 
	{
		this.branchService.getBranch().subscribe(data =>{
			this.branchList = data;
		})
		this.departmentService.getAllDepartments().subscribe(data => 
		{
			console.log(data),this.departments = data, this.len = data.length;
		});
	}

	details(data)
	{
		this.dep = data;
		this.submitted = !this.submitted;
		this.id1 = this.dep.departmentId;	
	}
	
	OnSubmit(form)
	{
	
		this.branchId = form.site;
		console.log(this.branchId);
		console.log(form.value);
		for(let branch of this.branchList){
			if(this.branchId == branch.id){
				this.dep.site = branch;
			}
		}
		this.departmentService.updateDepartment(this.id1, this.dep).subscribe(data => {
        this.gotoList();
      }, error => console.log(error));
  	}

	gotoList()
	{
		this.reloadData();
		//this.router.navigate(['/master/department']).then(() => {window.location.reload()});
	}
	
	newDepartment(){
		this.submit = !this.submit;
		this.submitted = true;
	}
	
	onSubmit(createDocForm : NgForm){
		this.department = createDocForm.value;
			console.log("This is the CreateDocForm data" + createDocForm)
		this.departmentService.createDepartments(this.department).subscribe(data => {
			console.log("This is data from creating department" + data);
			this.gotoList();
		})
	}
	
	showDepartments(){
		this.submitted = true;
		this.submit = true;
	}
}
