import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CountryCategoryComponent } from './country-category/country-category.component';
import { DistrictComponent } from './country-category/modules/district/district.component';
import { StateComponent } from './country-category/modules/state/state.component';
import { DepartmentComponent } from './department/department.component';
import { LookupCategoryComponent } from './lookup-module/components/lookup-category/lookup-category.component';
import { LookupEntityComponent } from './lookup-module/components/lookup-entity/lookup-entity.component';

import { MasterComponent } from './master.component';
import { ServiceCenterComponent } from './service-center/service-center.component';
import { UnitMasterComponent } from './unit-master/unit-master.component';

const routes: Routes = [

	{path: '' , component : MasterComponent},
	{path: 'lookup', component: LookupCategoryComponent},
	{path: 'department', component: DepartmentComponent},
	{path: 'country', component: CountryCategoryComponent},
	{path: 'lookup-entity/:id', component: LookupEntityComponent},
	{path: 'state/:id', component: StateComponent},
	{path: 'district', component: DistrictComponent},
	{path: 'serviceCenter', component: ServiceCenterComponent},
	{path: 'unitmaster', component: UnitMasterComponent},
	{path: 'itemmaster', loadChildren: () => import("./item-master/item-master.module").then((m) => m.ItemMasterModule)},
	
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MasterRoutingModule { }
