import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LookupCategoryComponent } from './components/lookup-category/lookup-category.component';
import { LookupEntityComponent } from './components/lookup-entity/lookup-entity.component';


const routes: Routes = [
	{path: 'lookupCategory', component : LookupCategoryComponent},
	{
		path: 'lookupEntity',component: LookupEntityComponent
	}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LookupModuleRoutingModule { }
