import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { routerTransition } from '../../router.animations';
import { Country } from './masters/country';
import { PatientAddressService } from './services/patient-address.service';


@Component({
  selector: 'app-country-category',
  templateUrl: './country-category.component.html',
  styleUrls: ['./country-category.component.css'],
  animations: [routerTransition()]
})
export class CountryCategoryComponent implements OnInit {
	country :Country;
	data: Country = new Country;
	createCountry:Country = new Country();
  	submitted :boolean = true;
	submit: boolean = true;
	name = '';
	page = 1;
	pageSize = 5;
	len : number;
  constructor(private http: HttpClient, private router: Router,private patietnAddresService: PatientAddressService) { }

  ngOnInit(): void 
	{
		this.reloadData();
	}
	
	reloadData()
	{
		this.patietnAddresService.getCountries().subscribe(data => {this.country =data,console.log(this.country), this.len = data.length});;	
	}

	details(country)
	{
		this.submitted = !this.submitted;
		this.submit = true;
		this.createCountry = country;
	}

	gotoStates(countryId: any)
	{
		console.log(countryId);
		this.router.navigate(['/master/state',countryId])
	}
	goto(){
		this.submitted = !this.submitted;
	}
	
	newCountry(){
		this.submitted = !this.submitted;
		this.submit = false;
		this.createCountry = new Country;
	}
	
	onSubmit(): any
	{
		this.patietnAddresService.createCountry(this.createCountry).subscribe(data => 
		{
			console.log("This is return after saving "+data);
			this.submitted = !this.submitted;
			this.reloadData();
		})
	}
	updateCountry(country){
		this.patietnAddresService.updateCountry(country.countryId,country).subscribe(data =>{
			alert("Updated Successfully");
			this.createCountry = new Country;
			this.reloadData();
			this.submitted = true;
		});
	}
	deleteCountry()
	{
		this.patietnAddresService.deleteCountry(this.data.countryId).subscribe(data =>
		{
			console.log(data);
			this.reloadData();
			this.submitted = !this.submitted;
		})
	}
	show(){
		this.submitted = true;
		this.submit = true;
	}
	


}
