import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CountryCategoryComponent } from './country-category.component';


const routes: Routes = [

	{path: '' , component : CountryCategoryComponent},
	{path: 'itemmaster', loadChildren: () => import("../item-master/item-master.module").then((m) => m.ItemMasterModule)},
	
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CountryCategoryRoutingModule { }
