import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { routerTransition } from '../../router.animations';
import { LookupEntity } from '../lookup-module/masters/lookup-entity';
import { LookupService } from '../lookup-module/services/lookup.service';
import { AlternateItem } from './masters/alternate-item';
import { InventoryAccount } from './masters/inventory-account';
import { Item } from './masters/item';
import { ItemBranchAccountMap } from './masters/item-branch-account-map';
import { ItemCategory } from './masters/Item-category';
import { ItemClass } from './masters/Item-class';
import { ItemSpecification } from './masters/item-specification';
import { ItemStores } from './masters/item-stores';
import { ItemSuppliers } from './masters/item-suppliers';
import { ItemType } from './masters/item-type';
import { PurchaseOrganization } from './masters/purchase-organization';
import { UOM } from './masters/UOM';
import { UOMType } from './masters/UOMType';
import { ItemMasterService } from './services/item-master.service';

@Component({
	selector: 'app-item-master',
	templateUrl: './item-master.component.html',
	styleUrls: ['./item-master.component.css'],
	animations: [routerTransition()]
})
export class ItemMasterComponent implements OnInit {
	createItemForm = new FormGroup({});
	item: Item = new Item;
	itemList: Array<Item> = new Array<Item>();
	len: number;
	page = 1;
	pageSize = 5;
	isList: boolean = true;
	searchItem = "";
	itemTypeList: Array<ItemType> = [];
	itemClassList: Array<ItemClass> = [];
	itemCategoryList: Array<ItemCategory> = [];
	uomList: Array<UOM> = new Array<UOM>();
	inventoryAccountList: Array<InventoryAccount> = [];
	uomTypeList: Array<UOMType> = [];
	itemSupplierList: Array<ItemSuppliers> = [];
	itemStoreList: Array<ItemStores> = [];
	alternateItemList: Array<AlternateItem> = [];
	itemSpecificationList: Array<ItemSpecification> = [];
	purchaseOrgList: Array<PurchaseOrganization> = [];
	itemBranchAccMapList: Array<ItemBranchAccountMap> = [];
	lookupEntityList: Array<LookupEntity> = [];
	uomId = 0;
	createItem: Item = new Item;
	itemTypeDemo :ItemType = new ItemType;
	constructor(private itemSerice: ItemMasterService, private fb: FormBuilder, private lookupService: LookupService) { }
	baseUom: UOM = new UOM;
	ngOnInit(): void {
		this.showItemList();
		

	}
	
	onSubmit() {
		console.log(this.createItemForm.value)
		this.createItem = this.createItemForm.value;
		this.itemTypeDemo = this.createItemForm.value.itemType;
		this.createItem.itemType = this.itemTypeDemo;
		this.baseUom = this.createItemForm.value.baseUom;
		this.createItem.baseUom = this.baseUom;
		this.itemSerice.createItem(this.createItem).subscribe(data => {
			console.log(data);
			alert("Inserted Successfuly")
		})
	}
	
	
	createItemFunc(){
		this.itemSerice.getItemTypeList().subscribe(data => {
			this.itemTypeList = data;
			console.log("ItemType", data);
		})
		this.itemSerice.getItemClassList().subscribe(data => {
			this.itemClassList = data;
			console.log("ItemClass", data);
		})

		this.itemSerice.getItemCategoryList().subscribe(data => {
			this.itemCategoryList = data;
			console.log("ItemCategory", data);
		})
		this.itemSerice.getItemCategoryList().subscribe(data => {
			this.itemCategoryList = data;
			console.log("ItemCategory", data);
		})
		this.itemSerice.getUOMList().subscribe(data => {
			this.uomList = data;
			console.log("UOM", data);
		})
		this.itemSerice.getInventoryAccontList().subscribe(data => {
			this.inventoryAccountList = data;
			console.log("InventoryAccont", data);
		})
		this.itemSerice.getUOMTypeList().subscribe(data => {
			this.uomTypeList = data;
			console.log("uomType", data);
		})
		this.itemSerice.getItemSuppliersList().subscribe(data => {
			this.itemSupplierList = data;
			console.log("ItemSuppliers", data);
		});
		this.itemSerice.getItemStoresList().subscribe(data => {
			this.itemStoreList = data;
			console.log("itemStore", data);
		})
		this.itemSerice.getAlternateItemList().subscribe(data => {
			this.alternateItemList = data;
			console.log("AlternateItem", data);
		});
		this.itemSerice.getItemSpecificationList().subscribe(data => {
			this.itemSpecificationList = data;
			console.log("ItemSpecification", data);
		});
		this.itemSerice.getPurchaseOrganisationList().subscribe(data => {
			this.purchaseOrgList = data;
			console.log("PurchaseOrganization", data);
		});
		this.itemSerice.getItemBranchAccountMapList().subscribe(data => {
			this.itemBranchAccMapList = data;
			console.log("itemBranchAccMapList", data);
		});
		this.lookupService.getAllLookupEntities().subscribe(data => {
			console.log(data);
			this.lookupEntityList = data;
		})

		this.createItemForm = this.fb.group({
			itemCode: new FormControl(null),
			itemName: new FormControl(null),
			itemDescription: new FormControl(null),
			arbicName: new FormControl(null),
			itemCategory: new FormControl(null),
			itemType: new FormControl(null),
			itemGroup: new FormControl(null),
			itemClass: new FormControl(null),
			stockType: new FormControl(null),
			procurementType: new FormControl(null),
			baseUom: new FormControl(null),
			trackUom: new FormControl(null),
			distributionCategory: new FormControl(null),
			purchaseOrg: new FormControl(null),
			selfLifeLimit: new FormControl(null),
			itemSpecification: new FormControl(null),
			sfda: new FormControl(null),
			inventoried: new FormControl(null),
			isBatchTracked: new FormControl(null),
			isExpiryDateRequired: new FormControl(null),
			isSerialized: new FormControl(null),
			active: new FormControl(null),
			isApprovalRequired: new FormControl(null),
			isInsuranceCover: new FormControl(null),
			purchaseUom: new FormControl(null),
			salesUOM: new FormControl(null),
			defaultPricingMethod: new FormControl(null),
			markupPercentage: new FormControl(null),
			itemBranchAccMapList: new FormControl(null),
			costOfSalesAccount: new FormControl(null),
			purchaseAccount: new FormControl(null),
			salesAccount: new FormControl(null),
			vedAnalysisType: new FormControl(null),
			cycleCountFrequency: new FormControl(null),
			reusable: new FormControl(null),
			reusableCount: new FormControl(null),
			itemRate: new FormControl(null),
			reserveQty: new FormControl(null),
			fsnType: new FormControl(null),
			manufacturerName: new FormControl(null),
			bulky: new FormControl(null),
			itemStores: new FormControl(null),
			sellingPrice: new FormControl(null),
			salesPricingMethod: new FormControl(null),
			remarks: new FormControl(null),
			currentCost: new FormControl(null),
		})
	}
	

	showItemList() {
		this.itemSerice.getItemList().subscribe(itemData => {
			//console.log(data);
			this.itemList = itemData;
			this.len = itemData.length;
			//console.log(this.len);
			this.isList = true;
		})
	}
	
	addItem() {
		this.isList = false;
		this.createItemFunc();
	}
	
	trackUom(uom) {
		this.uomId = uom.uomId;
		console.log(uom.uomId)

	}

}
