import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { LookupCategory } from '../../masters/lookup-category';
import { LookupEntity } from '../../masters/lookup-entity';
import { LookupService } from '../../services/lookup.service';


@Component({
  selector: 'app-lookup-entity',
  templateUrl: './lookup-entity.component.html',
  styleUrls: ['./lookup-entity.component.css']
})
export class LookupEntityComponent implements OnInit 
{
	createLookupEntity: LookupEntity = new LookupEntity();
	lookupEntity : Observable<LookupEntity> ;
	lookups : LookupCategory = new LookupCategory();
	
	categoryId : number ;
	submitted: boolean = true;
	submit: boolean = true;
	page = 1;
	pageSize =5;
	len : number;
	name = "";
 	constructor(private http: HttpClient, private route: ActivatedRoute,private lookupService: LookupService) { }

	
	
 	ngOnInit(): void 
	{
		
		this.categoryId= this.route.snapshot.params['id'];
		this.reloadData(this.categoryId);
  	}

	reloadData(id: number)
	{
		this.lookupService.getLookupEntityList(id).subscribe(data => {
			this.len = data.length;
			this.lookupEntity = data,
			this.submitted = true;
		});
		this.lookupService.getLookupCategoryById(this.categoryId).subscribe(data =>
			{
				this.lookups = data;
				console.log("This is lookupCategory Data " + data)
			})
	}
	
 	onSubmit(addLookup) 

	{
		this.createLookupEntity = addLookup.value;
		this.createLookupEntity.category = this.lookups;
		
    	this.createLookup(this.createLookupEntity); 
		console.warn(this.createLookupEntity.lookupId)
		console.log("categoryId : "+this.categoryId +"category is "+this.createLookupEntity.category.createdBy);
		
  	}

	goto()
	{
		this.submitted = !this.submitted;
		this.submit = !this.submit;
	}
	details(lookup){
		this.submitted = !this.submitted;
		this.createLookupEntity = lookup;
		this.submit = true;
	}
	
	createLookup( entity: any)
	{
	
		this.lookupService.createLookupEntity(entity).subscribe(data => 
		{
        	
		this.reloadData(this.categoryId);
      	}, error => console.log(error));				
  	}
	
	updateLookupEntity(lookup : any){
		//console.log("Update button is working")
		//console.log(lookup);
		this.lookupService.updateLookupEntity(lookup.lookupId,lookup).subscribe(data => {
        //console.log(data);
        this.lookups = new LookupCategory();
		this.createLookupEntity = new LookupEntity();
        this.reloadData(this.categoryId);
		alert("Updated Successfully");
      }, error => console.log(error));
	}
	
	deleteLookupEntity(lookupId){
		this.lookupService.deleteLookupEntity(lookupId).subscribe(data =>
		{
			//console.log(data);
			this.reloadData(this.categoryId);
		})
	}
	addLookup(){
		this.submitted = false;
		this.submit = false;
		this.createLookupEntity = new LookupEntity();
	}
}
