import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal, NgbModalConfig } from '@ng-bootstrap/ng-bootstrap';
import { Department } from '../department/masters/department';
import { DepartmentService } from '../department/services/department.service';
import { LookupEntity } from '../lookup-module/masters/lookup-entity';
import { LookupService } from '../lookup-module/services/lookup.service';
import { Employee } from './masters/Employee';
import { EmployeeService } from './masters/employee-master.service';
import { Unit } from './masters/unit';
import { UnitEmployeeMapping } from './masters/unitEmployeeMapping';
import { UnitMasterService } from './services/unit-master.service';


@Component({
  selector: 'app-unit-master',
  templateUrl: './unit-master.component.html',
  styleUrls: ['./unit-master.component.css']
})
export class UnitMasterComponent implements OnInit {

  constructor(
	private unitMasterService: UnitMasterService,
	private departmetnService: DepartmentService,
	private lookupEntityService: LookupService,
	private employeeService: EmployeeService,
	private config: NgbModalConfig,
	private modalService: NgbModal,
	private router: Router,
	) { 
	config.backdrop = 'static';
    config.keyboard = false;
	}
	unitList: Array<Unit> = [];
	unit: Unit = new Unit();
	unitEmployeeList: Array<UnitEmployeeMapping>; 
	unitEmployeeRemainingMappingArray: Array<UnitEmployeeMapping>;
	employeeDetail: Employee; 
	unitEmployeeMappingList: Array<UnitEmployeeMapping>;
	unitEmployeeMapping: UnitEmployeeMapping;
	departmentList: Array<Department>;
	lookupEntityList: Array<LookupEntity>;
	department: Department;
	submitted: boolean = true;
	isDetail : boolean = true;
	isAdd: boolean = false;
	len : number; 
	empLen: number;
	empUnitlen: number;
	page =1;
	empPage = 1;
	empUnitPage = 1;
	pageSize =4;
	name = "";
	empsearch = "";
	unitEmpSearch = "";
	unitId: number;
	departmentId : number;
	empcontent: any;
	
  ngOnInit(): void 
    {
	this.reloadData();
  	}
	reloadData()
	{
		this.unitMasterService.getUnit().subscribe(data =>
		{
			//console.log(data);
			this.unitList = data;
			this.len = data.length;
			
		});
		this.lookupEntityService.getAllLookupEntities().subscribe(data =>{
			this.lookupEntityList = data;
		});
		this.unitMasterService.getUnitEmployeeMapping().subscribe(data =>{
			this.unitEmployeeMappingList = data;
			//console.warn(this.unitEmployeeMappingList)
		});
		this.departmetnService.getAllDepartments().subscribe(data =>{
			this.departmentList = data;
		});
	}
	showEmployee(unit)
	{
		this.unitEmployeeList = [];
		this.unitEmployeeList = unit.unitEmployeeMappingList;
		this.empLen = unit.unitEmployeeMappingList.length;
		this.departmentId = unit.department.departmentId;
		console.log(this.departmentId);
		this.unitId = unit.unitId;
		this.unit = unit;
		//console.log(unit);
		this.submitted = false;
	}
	closeEmployee(){
		this.submitted = true;
	}
	employeeDetails(employee){
		this.employeeDetail = employee;
		this.isDetail = false;
	}
	updateEmployeeDetails(){
		this.employeeService.updateEmployee(this.employeeDetail.employeeId,this.employeeDetail).subscribe(data =>{
			console.log(data);
			alert("updated Successfully!!");
		})
		
	}
	closeEmployeeDetails(){
		this.isDetail = true;
	}
	addUnit(content){
		this.isAdd = false;
		this.unit = new Unit();
		this.open(content);
	}
	createUnit(newUnitForm: NgForm){
		console.log(newUnitForm.value)
		for (let dept of this.departmentList){
			if(dept.departmentId == newUnitForm.value.department)
			{
				this.unit.department = dept;
			}
		}
		alert(this.unit.department.departmentId)
			this.unitMasterService.createUnit(this.unit).subscribe(data =>
		{
			this.modalService.dismissAll();
			console.log(data);
			this.reloadData();
			this.isAdd = true;
		});
	}
	addEmployee(empcontent){
		this.unitMasterService.getOnlyRemainingEmployee(this.departmentId,this.unitId).subscribe(data =>{
			
			this.unitEmployeeRemainingMappingArray = data;
			this.empUnitlen = this.unitEmployeeRemainingMappingArray.length;
			console.error(this.unitEmployeeRemainingMappingArray);
		});
		this.empcontent = empcontent;
		this.modalService.open(empcontent, { size: 'lg'});
	}
	open(content) {
    this.modalService.open(content, { size: 'lg'});
  }
	updateUnit(unitForm){
		this.unitMasterService.updateUnit(this.unit.unitId, this.unit).subscribe(data =>{
			console.log(data);
			alert("Updated successfully");
			this.reloadData();
		this.modalService.dismissAll();
		});
		
	}
	addEmployeeToUnit(unitEmployeeMapping){
		console.warn(this.unitEmployeeList);
		this.unitEmployeeList.push(unitEmployeeMapping);
		console.warn(this.unitEmployeeList);
		
		this.unit.unitEmployeeMappingList = this.unitEmployeeList;
		this.unitMasterService.updateUnit(this.unitId, this.unit).subscribe(data =>{
			//console.log(data);
			this.modalService.dismissAll();
			this.addEmployee(this.empcontent);
			this.reloadData();
		});
	}
	update(unit,content){
		this.isAdd = true;
		this.modalService.open(content,{size: 'lg'});
		this.unit = unit;
	}
	backToMaster(){
		//console.log("Back to Master clicked!!!!!")
		this.router.navigate(['/master'])
	}
}
