import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { routerTransition } from '../../../../router.animations';
import { LookupCategory } from '../../masters/lookup-category';
import { LookupService } from '../../services/lookup.service';

@Component({
  selector: 'app-lookup-category',
  templateUrl: './lookup-category.component.html',
  styleUrls: ['./lookup-category.component.css'],
  animations: [routerTransition()]
})
export class LookupCategoryComponent implements OnInit 
{
	lookupCategory : Observable<LookupCategory>;
	data :LookupCategory = new LookupCategory();
	lookup = '';
	submitted :boolean = true;
	isDetail : boolean = true;
	name = '';
	page = 1;
	pageSize = 5;
	len : number;
	createLookup : LookupCategory = new LookupCategory();
  constructor(private http: HttpClient, private router: Router,private lookupService: LookupService) { }

  	ngOnInit(): void 
	{
		this.reloadData();
	}
	
	reloadData()
	{
		this.lookupService.getLookupCategoryList().subscribe(data => {this.lookupCategory =data,console.log(this.lookupCategory), this.len = data.length});;	
	}

	details(lookup)
	{
		this.submitted = !this.submitted;
		this.createLookup = lookup;
		this.isDetail = false;
	}

	gotoLookupEntity(categoryId: any)
	{
		//console.log(categoryId);
		this.router.navigate(['/master/lookup-entity',categoryId])
	}
	goto(){
		this.submitted = !this.submitted;
	}
	
	newLookupCategory(){
		this.submitted = !this.submitted;
		this.isDetail = true;
		this.createLookup = new LookupCategory();
	}
	
	createLookupCategory(): any
	{
		this.lookupService.createLookupCategory(this.createLookup).subscribe(data => 
		{
			//console.log("This is return after saving "+data);
			this.submitted = !this.submitted;
			console.log(data);
			this.reloadData();
		})
	}
	updateLookupCategory(){
		this.lookupService.updateLookupCategory(this.createLookup.categoryId,this.createLookup).subscribe(data =>{ 
			this.reloadData();
			this.show();
		})
	}
	deleteLookupCategory()
	{
		this.lookupService.deleteLookupCategory(this.data.categoryId).subscribe(data =>
		{
			console.log(data);
			this.reloadData();
			this.submitted = !this.submitted;
		})
	}
	show(){
		this.submitted = true;
	}
	

}















